.block{
    width: 500px;
    color: rgb(0, 0, 0);
    height: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 3px solid;
    padding: 10px;
    margin: 20px;
    cursor: pointer;
}

.img{
    border-radius: 10%;
    width: 350px;
    height:  180px;
}